/* MatchPlayerDetails.module.css */

.detailsContainer {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.title {
    font-size: 1.5rem;
    margin-bottom: 15px;
    color: #333;
}

.playerList {
    list-style: none;
    padding: 0;
}

.playerItem {
    background-color: #fff;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
}
