/* Login.module.css */
.loginMainContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 95vh;

}
.loginContainer {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 300px;
    text-align: center;
}

.loginContainer h2 {
    margin: 0 0 20px;
    color: #333;
}

.loginForm {
    display: flex;
    flex-direction: column;
}

.formGroup {
    margin-bottom: 15px;
}

.formGroup label {
    display: block;
    margin-bottom: 5px;
    text-align: left;
    color: #555;
}

.formGroup input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
}

.submitButton {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
}

.submitButton:hover {
    background-color: #0056b3;
}

.signUpText {
    font-size: 1rem;
    color: #555;
    text-align: center;
}

.signUpText a {
    color: #007bff; /* Bootstrap primary color or any color you prefer */
    text-decoration: none; /* Remove underline */
    font-weight: bold;
    transition: color 0.3s, text-decoration 0.3s;
}

.signUpText a:hover {
    color: #0056b3; /* Darker color for hover effect */
    text-decoration: underline; /* Add underline on hover */
}

.absoluteHeading{
    color: #003366;
    padding: 0 20px;
    font-style: italic;
    position: absolute;
}