.dashboardContainer{
    width: 1300px;
    /*background-color: #61dafb;*/
    margin: auto
}
.dashboardContainer h1{
    text-align: center;
}
nav{
    background-color: #333;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
}

.dashboardNavUl{
    /*margin-left: 60px;*/
    padding: 0;
    list-style: none;
    display: flex;
    flex: 1;
    justify-content: center;
    gap: 40px;
    align-items: center;

}

nav ul li{

}
nav ul li a{
    font-size: 19px;
    font-weight: bold;
    color: white;
    text-decoration: none;
}
nav button{
    text-align: right;
    margin-right: 0;
}
.rightLink{
    margin-left: auto;
    width: 262px;
}

.rightLink button{
    padding: 10px 15px;
    border: none;
    margin-right: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.brandHeading{
    color: #f4ffa2;
    padding: 0 20px;
    font-style: italic
}