/* PlayerStats.module.css */

.container {
    max-width: 800px;
    margin: 20px auto;

    padding: 20px;

    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    border-radius: 10px;

}

.styleForh1{
    text-align: center;
    color: #444;
}



.topPerformers {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

.playerCard {
    flex: 1;
    margin: 0 10px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    text-align: left;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.playerCard h2 {
    color: #007BFF;
}

.playerSelector {
    margin: 20px 0;
    text-align: left;
}

.playerSelector label {
    margin: 20px 0;
    margin-left: 20px;
    text-align: left;
    font-weight: bold;
}

.playerSelector select {
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-left: 20px;
}

.selectedPlayerStats {
    margin-top: 20px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.selectedPlayerStats h2 {
    color: #007BFF;
}

.topPerformersHead {
    margin-left: 20px;
}

.playerNameHead {
    margin-left: 20px;
    margin-top: 20px;
    background-color: #ecffc3;
    display: inline;
    padding: 10px 16px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
}

.selectedPlayerStatsContainer {
    margin-top: 30px;
}

.selectedPlayerStats p span {
    font-weight: bold;
}


.statsHead{
    font-weight: bold;
}


.absoluteHeading{
    color: #003366;
    padding: 0 20px;
    font-style: italic;
    position: absolute;
}
.spacingDiv{
    width:100%;
    height: 7vh;
}