/* MatchDetails.module.css */

.matchDetailsContainer {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.styleForh1 {
    text-align: center;
    /*color: #333;*/
    color: #444;
}



.recentMatches, .matchSelection, .matchDetail {
    margin-bottom: 20px;
}

.recentMatches ul {
    list-style-type: none;
    padding: 0;
}

.recentMatches li {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
}

.matchSelection label {
    display: block;
    margin-bottom: 10px;
}
.matchDetailsHead{
    display: block;
    /*margin-bottom: 10px;

     */
    font-weight: bold;
}

.matchSelection select {
    padding: 10px;
    /*border: 1px solid #ddd;*/
    border: 1px solid #949494;
    border-radius: 4px;
    width: 100%;
    max-width: 400px;
}

.matchDetail {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.matchDetail p {
    margin: 10px 0;
}

.matchDetail h3 {
    margin-top: 20px;
    color: #333;
}

.rightAlign {
    text-align: right;
    margin-right: 0;
}

.displayInline {
    display: inline-block;
}

.recentMatchDate {
    /* Add styles if needed */
}

.liScore {
    background-color: #ecffc3;
    font-weight: bold;
    padding: 6px 10px;
    border-radius: 5px;
}

.recentMatchBolds {
    font-weight: bold;
}

.matchId {
    color: #007BFF;
    font-weight: bold;
    display: inline;
}
.matchSelection {
    display: flex;
    gap: 10px;
    width: 100%;
    justify-content: unset;


}


.absoluteHeading{
    color: #003366;
    padding: 0 20px;
    font-style: italic;
    position: absolute;
}
.spacingDiv{
    width:100%;
    height: 7vh;
}